#startButton {
    position:relative;
    border-style:solid;
    border-width:3px;
    border-color:darkslategray;
    background-color:lightgray;
    color:darkslategray;
    padding:10px;
    font-weight:bold;
    cursor:pointer;
    outline:none;
}
#startButton:hover {
    background-color:pink;
    border-color:#E535AB;
    color:#E535AB;
    animation: flash 0.2s linear 0s 1;
}
#startButton:active {
    border-color:darkslategray;
    background-color:silver;
    color:darkslategray;
}
@keyframes flash {
    0% { background-color:pink;border-color:#E535AB;color:#E535AB; }
    20% { background-color:pink;border-color:#E535AB;color:#E535AB; }
    21% { background-color:lightgrey;border-color:darkslategrey;color:darkslategrey; }
    41% { background-color:lightgrey;border-color:darkslategrey;color:darkslategrey; }
    42% { background-color:pink;border-color:#E535AB;color:#E535AB; }
    62% { background-color:pink;border-color:#E535AB;color:#E535AB; }
    63% { background-color:lightgrey;border-color:darkslategrey;color:darkslategrey; }
    83% { background-color:lightgrey;border-color:darkslategrey;color:darkslategrey; }
    84% { background-color:pink;border-color:#E535AB;color:#E535AB; }
    100% { background-color:pink;border-color:#E535AB;color:#E535AB; }
}