.displayOptionBtn {
    border-style:solid;
    border-width:1.5px;
    border-color:gray;
    background-color:lavenderblush;
    color:gray;
    padding:7px;
    margin:0 5px 5px 5px;
    font-weight:bold;
    cursor:pointer;
    outline:none;
    display:flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}
.displayOptionBtn:hover {
    background-color:pink;
    border-color:#E535AB;
    color:#E535AB;
    z-index:2;
}
.displayOptionBtn img {
    width:20px;
    height:20px;
    margin-right:5px;
    filter: invert(57%) sepia(0%) saturate(141%) hue-rotate(165deg) brightness(88%) contrast(87%);
}
.displayOptionBtn:hover img {
    filter: invert(44%) sepia(81%) saturate(6198%) hue-rotate(300deg) brightness(97%) contrast(85%);
}
.displayOptionBtn:active {
    border-color:gray;
    background-color:silver;
    color:gray;
}
.displayOptionBtn:active img {
    filter: invert(57%) sepia(0%) saturate(141%) hue-rotate(165deg) brightness(88%) contrast(87%);
}
.configPanelOptionBtn {
    border-style:solid;
    border-width:3px;
    border-color:gray;
    background-color:lavenderblush;
    color:gray;
    padding:15px;
    font-weight:bold;
    cursor:pointer;
    outline:none;
}
.configPanelOptionBtn:hover {
    background-color:pink;
    border-color:#E535AB;
    color:#E535AB;
}
.configPanelOptionBtn:active {
    border-color:gray;
    background-color:silver;
    color:gray;
}
.parentBtn {
    border-style:solid;
    border-width:1px;
    border-color:gray;
    background-color:lavenderblush;
    color:gray;
    cursor:pointer;
    outline:none;
}
.parentBtn:hover {
    background-color:pink;
    border-color:#E535AB;
    color:#E535AB;
}
.parentBtn:active {
    border-color:gray;
    background-color:silver;
    color:gray;
}
.displayEntityContainer:hover {
    z-index: 2;
}
.displayEntityBtn {
    background-color:rgba(130, 130, 130, 0.8);
    color:Gainsboro;
    cursor:pointer;
    outline:none;
    height:20px;
    border-radius: 8px;
    box-shadow: 0px 0px 30px black;
}
.displayEntityBtn:hover {
    font-weight: bold;
}
.displayEntityBtn:active {
    border-color:gray;
    background-color:silver;
    color:gray;
}
#ConfigPanel {
    box-sizing: border-box;
    padding:10px 20px;
}
#ConfigPanel h1, #ConfigPanel h2, #ConfigPanel h3, #ConfigPanel h4, #ConfigPanel h5, #ConfigPanel h6 {
    padding:0;
    margin:0;
}
.relationshipTableBtn {
    width:100%;
    height:51px;
    cursor:pointer;
}
.configBtn {
    width:100%;
    cursor:pointer;
    outline:none;
}
#PseudonymStickersSubSec {
    display:flex;
    flex-direction: row;
}
.sticker {
    display:flex;
    flex-direction:row;
    justify-content:space-between;
    align-items:center;
    padding: 1px 3px;
    cursor:pointer;
}
.stickerEx {
    width:10px;
    height:10px;
    margin-left:3px;
}
.sticker:hover .stickerEx {
    transform: scale(1.3,1.3);
}
.configPanelSec {
    transform: scale(0.8,0.8);
    /* opacity:1; */
    /* transition: transform 1s; */
    /* animation-name: slow-appear;
    animation-duration: 1.1s; */
}
/* @keyframes slow-appear {
    0% { opacity: 0; }
    90% { opacity: 0; }
    100% { opacity: 1; }
} */
.configPanelSec:hover {
    transform: scale(1,1);
    animation-name: zoom;
    animation-duration: 1s;
}
@keyframes zoom {
    0% { transform: scale(0.8,0.8); }
    100% { transform: scale(1,1); }
}
.sectionDivider {
    border-style:solid;
    border-width:1px 0 0 0;
    border-color:black;
    margin: 10px 50px;
}
.confirmBtn {
    cursor:pointer;
    width: 48%;
    padding: 10px 0;
    outline:none;
    color:black;
}
.confirmBtn:hover {
    font-weight: bold;
    border-style: solid;
    border-width: 2px;
    border-color: dimgray;
    color:dimgray;
}
.confirmBtn:active {
    font-weight: bold;
    border-style: solid;
    border-width: 2px;
    border-color: dimgray;
    background-color: darkgray;
    color:dimgray;
}
#parentPanelOpenTab:hover div#parentArrowContainer img#parentArrow {
    transform: scale(1.3,1.3);
}
.stickersBox {
    display:flex;
    flex-direction:row;
    justify-content:flex-start;
    align-items:center;
    flex-wrap:wrap;
}
.error {
    width:100%;
    max-width:100%;
    background-color:rgba(255,0,0,0.5);
    border-style:solid;
    border-width:1px;
    border-color:black;
    display:flex;
    flex-direction:row;
    justify-content:space-between;
    align-items:center;
    padding:3px 5px;
    box-sizing:border-box;
    height:auto;
    overflow:hidden;
}
.error:hover {
    background-color:rgba(255,0,0,0.8);
}
.errorEx:hover {
    transform: scale(1.3,1.3);
}